import request from 'request'

function LanchesDAO() {

}

LanchesDAO.prototype.pegarTodosLanches = (cb) => {
    const host = 'http://localhost:52495/api/'
    const path = 'Lanches'
    return request(`${host}${path}`, (err, body) => {
        cb(err, body)
    })


}

module.exports = function () {
    return LanchesDAO
}